import { Component, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import { Observable } from "rxjs/Observable";

import { ArticleGroupService } from "../article-group/article-group.service";
import { ArticleGroup } from "../article-group/article-group";

@Component({
  selector: "app-article-groups",
  templateUrl: "./article-groups.component.html",
  styleUrls: ["./article-groups.component.css"]
})
export class ArticleGroupsComponent implements OnInit {
  articleGroups: Observable<ArticleGroup[]>;

  constructor(
    private articleGroupService: ArticleGroupService,
    private router: Router
  ) {}

  ngOnInit() {
    this.articleGroups = this.articleGroupService.getGroups();
  }

  openGroup(group: ArticleGroup) {
    this.router.navigate(["/ag/", group.agCode]);
  }
}



