import { Component, OnInit } from "@angular/core";
import { Router, ActivatedRoute } from "@angular/router";
import { Observable } from "rxjs/Observable";

import { ArticleService } from "../article/article.service";
import { Article } from "../article/article";

@Component({
  selector: "app-article-group",
  templateUrl: "./article-group.component.html",
  styleUrls: ["./article-group.component.css"]
})
export class ArticleGroupComponent implements OnInit {
  articles: Observable<Article[]>;

  constructor(
    private activatedRoute: ActivatedRoute,
    private articleService: ArticleService,
    private router: Router
  ) {}

  ngOnInit() {
    this.articles = this.activatedRoute.params
      .map(params => params.agCode)
      .switchMap(agCode => this.articleService.getArticles(agCode));
  }

  openArticle(article: Article) {
    this.router.navigate(["/art/", article.artCode]);
  }
}



