import { Injectable } from "@angular/core";
import { Http, RequestOptionsArgs, BaseRequestOptions } from "@angular/http";
import { Router } from "@angular/router";
import { Observable } from "rxjs/Observable";

import { Company } from "./company";

import "../rxjs.operators";

const avHost = "https://AccountView.net/api/v3";

@Injectable()
export class CompanyService {
  constructor(private http: Http, private router: Router) {}

  fetchCompanyId(): Observable<Company[]> {
    var options: RequestOptionsArgs = new BaseRequestOptions();
    options.headers.append(
      "authorization",
      `${localStorage.token_type} ${localStorage.access_token}`
    );

    return this.http
      .get(`${avHost}/companies`, options)
      .map(rsp => rsp.json() as Company[])
      .do((companies) => {
        if (companies.length) {
          localStorage.company_id = companies[0].Id;
        } else {
          throw new Error("No companies found.");
        }
      })
      .catch(err => {
        if (err.status === 401) {
          this.router.navigate(["/login"]);
        } else {
          console.warn(err);
        }
        return Observable.of([]);
      });
  }
}



