import { Component, OnInit, Input } from '@angular/core';
import { Router, ActivatedRoute } from "@angular/router";
import { Observable } from "rxjs/Observable";

import { ArticleService } from "./article.service";
import { Article } from './article';

@Component({
  selector: 'app-article',
  templateUrl: './article.component.html',
  styleUrls: ['./article.component.css']
})
export class ArticleComponent implements OnInit {

@Input()  article: Article;

  constructor() { }

  ngOnInit() {
  }

}



@Component({
  template: '<app-article [article]="article | async"></app-article>',
  styleUrls: ['./article.component.css']
})
export class ArticleFromRouteComponent implements OnInit {

  article: Observable<Article>;

  constructor(private activatedRoute: ActivatedRoute,    private articleService: ArticleService
) { }

  ngOnInit() {
    this.article = this.activatedRoute.params
      .map(params => params.artCode)
      .switchMap(artCode => this.articleService.getArticle(artCode));
  }

}



