const baseURL = "https://accountview.net/";
const scope = "readaccountviewdata";
const redirectUri = `${location.origin}/get-token.html`;

@Component({
  selector: "app-login-page",
  templateUrl: "./login-page.component.html",
  styleUrls: ["./login-page.component.css"]
})
export class LoginPageComponent implements OnInit {
  loginUrl: string;

  constructor() {}

  ngOnInit() {
    this.loginUrl = `${baseURL}ams/authorize.aspx?response_type=token&client_id=${clientId}&redirect_uri=${redirectUri}&scope=${scope}`;
  }
}



