import { Injectable } from "@angular/core";
import { Http, RequestOptionsArgs, BaseRequestOptions } from "@angular/http";
import { Observable } from "rxjs/Observable";
import { stringify } from "querystring";

import "../rxjs.operators";

import {HistoryType} from './history-type'

import { filterArrToObject, formatProperties } from "../utils";

const avHost = "https://AccountView.net/api/v3";

@Injectable()
export class HistoryService {
  constructor(private http: Http) {}

  getPurchases(artCode: string): Observable<History[]> {
    return this.getHistory(artCode, HistoryType.Purchase);
  }
  getSales(artCode: string): Observable<History[]> {
    return this.getHistory(artCode, HistoryType.Sales);
  }

  getHistory(artCode: string, historyType: HistoryType): Observable<History[]> {
    var options: RequestOptionsArgs = new BaseRequestOptions();
    options.headers.append(
      "authorization",
      `${localStorage.token_type} ${localStorage.access_token}`
    );
    options.headers.append("x-company", localStorage.company_id);

    return (
      Observable.of(artCode)
        .map(artCode => [
          {
            controlSource: "art_code",
            value: artCode
          },
          {
            controlSource: "hst_ah.hdr_type",
            valueType: "N",
            value: historyType
          }
        ])
        .map(filter => filterArrToObject(filter))
        .map(filter =>
          Object.assign(
            {},
            {
              businessObject: "ALH",
              pageSize: 20,
              SortFields: "hst_ah.del_date",
              SortOrder: "descending",
              fields: [
                // "hst_al.rec_id",
                // "hst_ah.hdr_type",
                // "hst_ah.rpl_inv",
                "hst_ah.dc_name",
                // "hst_ah.inv_date",
                "hst_ah.del_date",
                // "hst_al.art_code",
                "hst_ah.ship_mth",
                "hst_al.unit_px",
                "hst_al.art_unit",
                "hst_al.pack_qty",
                "hst_al.pack_cont",
                "hst_al.k1_saw",
                "hst_al.k1_marge",
                "hst_al.eutr_lev",
                "hst_al.cost_px",
                "hst_al.l_costpx",
                // "hst_ah.po_dlb",
                "hst_al.direct_px",
                // "hst_al.vsrc_code",
                "hst_al.vpack_qty",
                "hst_ah.vdisc_desc",
                "hst_ah.ship_cost",
                "hst_ah.ord_cost",
                "hst_ah.comment1",
                "hst_ah.comment2",
                "hst_ah.del_addr"
              ].join(",")
            },
            filter
          )
        )
        .map(query => stringify(query))
        .map(query => `${avHost}/AccountViewData?${query}`)
        .switchMap(url => this.http.get(url, options))
        .map(rsp => rsp.json())
        .map(data => data.HST_AL)
        .map(data => formatProperties(data) as History[])
        // .map(data =>
        //   data.map(group => Object.assign(group, { qtyTech: 0, qtyBestel: 0 }))
        // )
        .catch(err => {
          console.warn(err);
          return Observable.of([]);
        })
        .do(console.table)
    );
  }
}



