import { BrowserModule } from "@angular/platform-browser";
import { NgModule } from "@angular/core";
import { RouterModule, Routes } from "@angular/router";
import { HttpModule } from "@angular/http";
import { NgbModule } from "@ng-bootstrap/ng-bootstrap";

import { AppComponent } from "./app.component";
import { LoginPageComponent } from "./login-page/login-page.component";
import { CompanyService } from "./utils/company.service";
import { ArticleGroupService } from "./article-group/article-group.service";
import { ArticleService } from "./article/article.service";
import { PackingService } from "./packing/packing.service";
import { HistoryService } from "./history/history.service";

import { ArticleGroupsComponent } from "./article-groups/article-groups.component";
import { ArticleGroupComponent } from "./article-group/article-group.component";
import { ArticleComponent, ArticleFromRouteComponent } from "./article/article.component";
import { PackingComponent } from "./packing/packing.component";
import { PurchasesComponent } from "./history/purchases/purchases.component";
import { SalesComponent } from "./history/sales/sales.component";

var routes: Routes = [
  {
    path: "login",
    component: LoginPageComponent
  },
  {
    path: "ag/:agCode",
    component: ArticleGroupComponent
  },
  {
    path: "art/:artCode",
    component: ArticleFromRouteComponent
  },
  {
    path: "",
    component: ArticleGroupsComponent
  }
];

if (!localStorage.access_token) {
  routes.push({
    path: "",
    redirectTo: "/login",
    pathMatch: "full"
  });
}

@NgModule({
  declarations: [
    AppComponent,
    LoginPageComponent,
    ArticleGroupsComponent,
    ArticleGroupComponent,
    ArticleComponent,
    ArticleFromRouteComponent,
    PackingComponent,
    PurchasesComponent,
    SalesComponent
  ],
  imports: [
    BrowserModule,
    HttpModule,
    RouterModule.forRoot(routes),
    NgbModule.forRoot()
  ],
  providers: [
    CompanyService,
    ArticleGroupService,
    ArticleService,
    PackingService,
    HistoryService
  ],
  bootstrap: [AppComponent]
})
export class AppModule {}



