import { Injectable } from "@angular/core";
import { Http, RequestOptionsArgs, BaseRequestOptions } from "@angular/http";
import { Observable } from "rxjs/Observable";
import { stringify } from "querystring";

import "../rxjs.operators";

import { ArticleGroup } from "./article-group";
import { formatProperties } from "../utils";
import { ArticleService } from "../article/article.service";

const avHost = "https://AccountView.net/api/v3";

@Injectable()
export class ArticleGroupService {
  allGroups$: Observable<ArticleGroup[]> = null;

  constructor(private http: Http, private articleService: ArticleService) {}

  getAllGroups() {
    if (!this.allGroups$) {
      var options: RequestOptionsArgs = new BaseRequestOptions();
      options.headers.append(
        "authorization",
        `${localStorage.token_type} ${localStorage.access_token}`
      );
      options.headers.append("x-company", localStorage.company_id);

      this.allGroups$ = Observable.of("")
        .map(filter =>
          Object.assign(
            {},
            {
              businessObject: "AG1",
              SortFields: ["ag_desc"].join(","),
              pageSize: 1000,
              fields: ["ag_code", "ag_desc"].join(",")
            }
          )
        )
        .map(query => stringify(query))
        .map(query => `${avHost}/AccountViewData?${query}`)
        .switchMap(url => this.http.get(url, options))
        .map(rsp => rsp.json())
        .map(data => data.ART_GRP)
        .map(data => formatProperties(data) as ArticleGroup[])
        .map(data =>
          data.map(group => Object.assign(group, { qtyTech: 0, qtyBestel: 0 }))
        )
        .catch(err => {
          console.warn(err);
          return Observable.of([]);
        })
        .publishReplay(1)
        .refCount()
        .combineLatest(
          this.articleService.getAllArticles(),
          (groups: any[], articles) => {
            groups.forEach(grp => {
              grp.qtyTech = articles.filter(
                art => art.agCode === grp.agCode && art.qtyTech
              ).length;
              grp.qtyBestel = articles.filter(
                art => art.agCode === grp.agCode && art.qtyBestel
              ).length;
            });

            return groups.filter(grp => grp.qtyTech || grp.qtyBestel);
          }
        )
        .do(console.table);
    }

    return this.allGroups$;
  }

  getGroups(): Observable<ArticleGroup[]> {
    return this.getAllGroups();
  }
}



