import camelCase from "lodash.camelcase";

export function formatProperties(data: any[]) {
  return data.map(json => {
    const result = {};
    Object.keys(json).forEach(key => (result[camelCase(key)] = json[key]));
    return result;
  });
}

export interface FilterItem {
  controlSource: string;
  operator?: string;
  valueType?: string;
  value: any;
  isListOfValues?: boolean;
}

export function filterArrToObject(items: FilterItem[]) {
  return items.reduce((filter, item, i) => {
    const nr = i + 1;
    filter["filterControlSource" + nr] = item.controlSource;
    filter["filterOperator" + nr] = item.operator || "equal";
    filter["filterValueType" + nr] = item.valueType || "C";
    filter["filterValue" + nr] = item.value;
    filter["filterIsListOfValues" + nr] = item.isListOfValues || false;
    return filter;
  }, {});
}



