import { Injectable } from "@angular/core";
import { Http, RequestOptionsArgs, BaseRequestOptions } from "@angular/http";
import { Observable } from "rxjs/Observable";
import { stringify } from "querystring";

import "../rxjs.operators";

import { Article } from "./article";
import { filterArrToObject, formatProperties } from "../utils";

const avHost = "https://AccountView.net/api/v3";

@Injectable()
export class ArticleService {
  allArticles$: Observable<Article[]> = null;

  constructor(private http: Http) {}

  getAllArticles() {
    if (!this.allArticles$) {
      var options: RequestOptionsArgs = new BaseRequestOptions();
      options.headers.append(
        "authorization",
        `${localStorage.token_type} ${localStorage.access_token}`
      );
      options.headers.append("x-company", localStorage.company_id);

      this.allArticles$ = Observable.of(0)
        .map(() => [
          {
            controlSource: "article.qty_tech",
            operator: "notequal",
            valueType: "N",
            value: 0
          },
          {
            controlSource: "article.qty_bestel",
            operator: "notequal",
            valueType: "N",
            value: 0
          }
        ])
        .map(filter => filterArrToObject(filter))
        .map(filter =>
          Object.assign(
            {},
            {
              businessObject: "AK1",
              pageSize: 1000,
              matchAnyFilter: true,
              fields: [
                "art_code",
                "ag_code",
                "art_desc1",
                "qty_free2",
                "cost_px",
                "l_costpx",
                "b_costpx",
                "k1_qdpx",
                "qty_tech",
                "qty_bestel",
                "k1_qtypm",
                "k1_qtypy"
              ].join(",")
            },
            filter
          )
        )
        .map(query => stringify(query))
        .map(query => `${avHost}/AccountViewData?${query}`)
        .switchMap(url => this.http.get(url, options))
        .map(rsp => rsp.json())
        .map(data => data.ARTICLE)
        .map(data => formatProperties(data) as Article[])
        .publishReplay(1)
        .refCount();
    }
    return this.allArticles$;
  }

  getArticles(agCode: string): Observable<Article[]> {
    return this.getAllArticles()
      .map(data => data.filter(art => art.agCode === agCode))
      .catch(err => {
        console.warn(err);
        return Observable.of([]);
      })
      .do(console.table);
  }

  getArticle(artCode: string): Observable<Article> {
    return this.getAllArticles()
      .mergeMap( e=> e)
      .filter(art => art.artCode === artCode)
      .catch(err => {
        console.warn(err);
        return Observable.of(null);
      })
      .do(console.log);
  }
}



